import "./recipes.css";
import { DataGrid } from '@mui/x-data-grid';

import { useState, useEffect } from "react";
import axios from "axios"
import { Link } from "react-router-dom";
import DeleteIcon from '@mui/icons-material/Delete';
export default function ProductList() {


  const [orders, setOrder] = useState([]);


  const handleDelete = (id) => {
    setOrder(orders.filter((item) => item.id !== id));
    axios.delete("http://localhost:3000/api/v1/recipes/"+id)
  };

  useEffect( () => {

    const fetchData = async () => {
      const result = await axios(
        "https://glitchhub.coffee/api/v1/recipes",
      );

      setOrder(result.data);
    };
    fetchData();

  },[]);





  const columns = [
    { field: "id", headerName: "Product", width: 200,editable: true },
    {
      field: "bean1Name",
      headerName: "Bean Name",
      width: 150,
      editable: true
    },
    {
      field: "bean1Amount",
      headerName: "Amount (g)",
      width: 107,
      editable: true
    },
    {
        field: "bean2Name",
        headerName: "Bean Name",
        width: 150,
        editable: true
      },
      {
        field: "bean2Amount",
        headerName: "Amount (g)",
        width: 107,
        editable: true
      },
      {
        field: "bean3Name",
        headerName: "Bean Name",
        width: 150,
        editable: true
      },
      {
        field: "bean3Amount",
        headerName: "Amount (g)",
        width: 107,
        editable: true
      },
      {
        field: "action",
        headerName: "Action",
        width: 150,
        renderCell: (params) => {
          return (
            <>
              <Link to={"/recipe/" + params.row.id}>
                <button className="productListEdit">Edit</button>
              </Link>
              <DeleteIcon
                className="productListDelete"
                onClick={() => handleDelete(params.row.id)}
              />
            </>
          );
        },
    }
  ];

  return (
    
    <div className="productList">

      {/* <ul>
        {orders.map(order => (
          <li key={order.id}>
            <p>{order.id}, {order.date}</p>
          </li>
        ))}
      </ul> */}
        <DataGrid
          rows={orders}
          disableSelectionOnClick
          columns={columns}
          pageSize={20}
          checkboxSelection
        />

    </div>
  );
}
